import React, { useState } from 'react';
import Map from './components/Map';
import Sidebar from './components/Sidebar';
import Output from './components/Output';


const App = () => {
  const [origin, setOrigin] = useState([77.516233, 28.494164]);
  const [destinations, setDestinations] = useState([]);
  const [showOutput, toggleShowOutput] = useState(false);

  return (
    <div className="App">
      <Sidebar destinations={destinations} setDestinations={setDestinations} origin={origin} setOrigin={setOrigin} showOutput={showOutput} toggleShowOutput={toggleShowOutput} />
      <Map origin={origin} setOrigin={setOrigin} destinations={destinations} setDestinations={setDestinations} />
      {showOutput ? (
        <Output showOutput={showOutput} toggleShowOutput={toggleShowOutput} origin={origin} destinations={destinations} />
      ) : (null)}
    </div>
  );
}

export default App;
