import React, { useState, useEffect } from 'react';
import ReactMapGL, { Marker, NavigationControl, GeolocateControl } from 'react-map-gl';
import originMarker from '../graphics/marker1.svg';
import destinationMarker from '../graphics/marker2.svg';
import earthIcon from '../graphics/earth.svg';
import mapIcon from '../graphics/regmap.svg';
import lineIcon from '../graphics/graph.svg';

const Map = ({origin, setOrigin, destinations, setDestinations}) => {
    const [viewPort, setViewPort] = useState({
        latitude: origin[1],
        longitude: origin[0], 
        width: "75vw",
        height: "100vh",
        zoom: 14
    });

    const [updateView, setUpdateView] = useState(true);

    const [showSatMap, toggleShowSatMap] = useState(false);

    const [drawLine, toggleDrawLine] = useState({draw: false, init: true});

    const handleMapClick = (e) => {
        if (drawLine.init) {
            toggleDrawLine({draw: true, init: false});
            return ;
        } else {
            let newCoord = e.lngLat;
            if (origin[0]) {
                setDestinations([...destinations, newCoord]);
            } else {
                setOrigin(newCoord);
        }
        }
    };

    const handleOriginDrag = (e) => {
        setUpdateView(false);
        setOrigin(e.lngLat);
    };

    useEffect(() => {
        if (updateView) {
            setTimeout(() => {
                setViewPort({
                    latitude: origin[1],
                    longitude: origin[0], 
                    width: "75vw",
                    height: "100vh",
                    zoom: 14 
                })
            }, 1000);
        }
        return () => {
            setUpdateView(true);
        }
    }, [origin])

    return ( 
        <div id="map">
            <ReactMapGL
            {...viewPort}
            mapboxApiAccessToken={"pk.eyJ1IjoiZmVybWl2ZXJzZSIsImEiOiJja2Q2YzJ3Zjkwam1mMnFuMG1zNjg5eDhmIn0.SwCaqFKXTC_-WkcbYqFcsQ"}
            mapStyle={showSatMap ? "mapbox://styles/mapbox/satellite-v9" : "mapbox://styles/fermiverse/ckd7yqq7u084t1iqj97nvbjc6"}
            onViewportChange={(viewPort) => {
                setViewPort(viewPort)
            }}
            onClick={drawLine.draw ? handleMapClick : null}>
                <div style={{position: 'absolute', right: "15px", top: "30px"}}>
                    <NavigationControl />
                </div>
                <div style={{position: "absolute", top: "135px", right: "15px"}}>
                    <GeolocateControl 
                    onViewportChange={(view) => {
                        setViewPort({
                            latitude: origin[1] || 28.494164,
                            longitude: origin[0] || 77.516233,
                            width: "75vw",
                            height: "100%",
                            zoom: 14
                        });
                    }}
                    />
                </div>
                <button className="line" title="Line string" onClick={() => {toggleDrawLine({draw: !drawLine.draw, init: true})}}>
                    <img src={lineIcon} alt="line" width="16px"></img>
                </button>
                {drawLine.draw ? (
                    <div style={{position: "absolute", left: "15px", top: "30px", color: "white"}}>
                        Draw Mode ON
                    </div>
                ) : (null)}
                {origin.length ? (
                    <Marker
                    latitude={origin[1]}
                    longitude={origin[0]}
                    draggable={true}
                    onDragEnd={handleOriginDrag}
                    offsetLeft={-16}
                    offsetTop={-38.71}>
                        <div title="origin">
                            <img src={originMarker} alt="marker" width="32px" className="marker"></img>
                        </div>
                    </Marker>
                ) : (null)}
                {destinations.map(destination => (
                    <Marker
                    key={String(destination[1])}
                    latitude={destination[1]}
                    longitude={destination[0]}
                    draggable={true}
                    onDragEnd={(e) => {
                        let modDests = destinations.slice();
                        let i = destinations.indexOf(destination);
                        let newCoords = e.lngLat;
                        modDests.splice(i, 1, newCoords);
                        setDestinations(modDests);
                    }}
                    offsetLeft={-16}
                    offsetTop={-38.71}>
                        <div title={`destination_${destinations.indexOf(destination)+1}`}>
                            <img src={destinationMarker} alt="marker" width="32px" className="marker"></img>
                        </div>
                    </Marker>
                ))}
                {showSatMap ? (
                    <button className="blank" id="toggle" title="Regular View" onClick={() => {toggleShowSatMap(!showSatMap)}}>
                        <img src={mapIcon} alt="mapicon" width="30px"></img>
                    </button>
                ) : (
                    <button className="blank" id="toggle" title="Satellite View" onClick={() => {toggleShowSatMap(!showSatMap)}}>
                        <img src={earthIcon} alt="sat" width="30px"></img>
                    </button>
                )}
                <button className="rounded" id="reset" onClick={() => {
                    toggleDrawLine({draw: false, init: true});
                    setUpdateView(false);
                    setOrigin([]);
                    setDestinations([]);
                }}>Reset</button>
            </ReactMapGL>
        </div>
     );
}

export default Map;

